import { Component, OnInit } from '@angular/core';
import { mergeMap } from 'rxjs/operators';
import { RetiredPool } from 'src/app/models/retired-pool';
import { RetiredVault } from 'src/app/models/retired-vault';
import { RetiredPool as BetaRetiredPool } from 'src/app/models/beta/retired-pool';
import { RetiredVault as BetaRetiredVault } from 'src/app/models/beta/retired-vault';
import { RetiredVault as V1RetiredVault } from 'src/app/models/v1/retired-vault';
import { RetiredPool as V1RetiredPool } from 'src/app/models/v1/retired-pool';
import { RetiredTradingVault as V1RetiredTradingVault } from 'src/app/models/v1/retired-trading-vault';
import { AccountService } from 'src/app/services/account.service';
import { ContractService } from 'src/app/services/contract.service';
import { EthWalletService } from 'src/app/services/eth-wallet.service';
import { LoadingService } from 'src/app/services/loading.service';
import { MessageService } from 'src/app/services/message.service';
import { PaymasterService } from 'src/app/services/paymaster.service';
import { environment } from 'src/environments/environment';
import { MainService } from 'src/app/services/main.service';

@Component({
  selector: 'app-index',
  templateUrl: './index.component.html',
  styleUrls: ['./index.component.scss']
})
export class IndexComponent implements OnInit {
  retiredPools: RetiredPool[];
  retiredVaults: RetiredVault[];
  betaRetiredPools: BetaRetiredPool[];
  betaRetiredVaults: BetaRetiredVault[];
  v1RetiredPools: V1RetiredPool[];
  v1RetiredVaults: V1RetiredVault[];
  v1RetiredTradingVaults: V1RetiredTradingVault[];
  searchAddress: string = '';

  constructor(
    public loadingService: LoadingService,
    public accountService: AccountService,
    private contractService: ContractService,
    private ethWalletService: EthWalletService,
    private messageService: MessageService,
    private paymasterService: PaymasterService,
    private mainService: MainService
  ) { }

  ngOnInit(): void {
    this.retiredPools = environment.retired.pools.map((p: any) => new RetiredPool(p.address, this.accountService, this.contractService, this.ethWalletService, p)) || [];
    this.retiredVaults = environment.retired.vaults.map((v: any) => new RetiredVault(v.address, this.accountService, this.contractService, this.ethWalletService, v)) || [];
    this.betaRetiredPools = environment.betaRetired.pools.map((p: any) => new BetaRetiredPool(p.address, this.accountService, this.contractService, this.ethWalletService, p)) || [];
    this.betaRetiredVaults = environment.betaRetired.vaults.map((v: any) => new BetaRetiredVault(v.address, this.accountService, this.contractService, v)) || [];
    this.v1RetiredPools = environment.v1Retired.pools.map((p: any) => new V1RetiredPool(p.address, this.accountService, this.contractService, this.ethWalletService, p)) || [];
    this.v1RetiredVaults = environment.v1Retired.vaults.map((v: any) => new V1RetiredVault(v.address, this.accountService, this.contractService, v)) || [];
    this.v1RetiredTradingVaults = environment.v1Retired.tradingVaults.map((v: any) => new V1RetiredTradingVault(v.address, this.accountService, this.contractService, this.ethWalletService, v)) || [];
  }

  withdrawVault(vault: RetiredVault | BetaRetiredVault | V1RetiredVault) {
    if (this.loadingService.loading) {
      return;
    }
    this.loadingService.start();

    this.paymasterService.getMethod().pipe(
      mergeMap((paymasterMethod) => vault.withdrawAll(paymasterMethod))
    ).subscribe(
      () => {
        this.loadingService.stop();
        this.messageService.success('Transaction completed.');
      },
      (e) => {
        this.loadingService.stop();
        this.messageService.handleError(e);
      }
    );
  }

  withdrawPool(pool: RetiredPool | BetaRetiredPool | V1RetiredPool) {
    if (this.loadingService.loading) {
      return;
    }
    this.loadingService.start();

    this.paymasterService.getMethod().pipe(
      mergeMap((paymasterMethod) => pool.withdrawAll(paymasterMethod))
    ).subscribe(
      () => {
        this.loadingService.stop();
        this.messageService.success('Transaction completed.');
      },
      (e) => {
        this.loadingService.stop();
        this.messageService.handleError(e);
      }
    );
  }

  withdrawTradingVault(tradingVault: V1RetiredTradingVault) {
    if (this.loadingService.loading) {
      return;
    }
    this.loadingService.start();

    this.paymasterService.getMethod().pipe(
      mergeMap((paymasterMethod) => tradingVault.withdrawAll(paymasterMethod))
    ).subscribe(
      () => {
        this.loadingService.stop();
        this.messageService.success('Transaction completed.');
      },
      (e) => {
        this.loadingService.stop();
        this.messageService.handleError(e);
      }
    );
  }

  onSearch() {
    if (this.searchAddress && (this.searchAddress.length !== 42 || !this.searchAddress.startsWith('0x'))) {
      this.messageService.error('Invalid address');
      return;
    }
    this.accountService.setObserveAddress(this.searchAddress);
  }

  clear() {
    this.searchAddress = '';
    this.onSearch();
  }
}
