import BigNumber from 'bignumber.js';
import PoolAbi from 'src/app/abi/pool';
import { AccountService } from '../../services/account.service';
import { ContractService } from '../../services/contract.service';
import { EthWalletService } from '../../services/eth-wallet.service';
import { Base } from '../base';
import { Observable, combineLatest, timer } from 'rxjs';
import { map, mergeMap, switchMap, take } from 'rxjs/operators';
import { Token } from '../token';
import { sharePublishReplay } from 'ngx-libs';
import { RetiredVault, signedData } from './retired-vault';
import { PaymasterMethod } from '../paymaster-method';

export class RetiredPool extends Base {
  name: string;
  totalShares: Observable<BigNumber>;
  equity: Observable<BigNumber>;
  myShares: Observable<BigNumber>;
  myValue: Observable<BigNumber>;
  staked: Observable<boolean>;

  constructor(
    public contract: string,
    protected accountService: AccountService,
    protected contractService: ContractService,
    private ethWalletService: EthWalletService,
    private info: any
  ) {
    super(contract, PoolAbi, accountService, contractService);
    this.name = this.info.name;

    const token = this.getToken().pipe(
      map((address) => new Token(address, 18, this.accountService, this.ethWalletService, this.contractService)),
      sharePublishReplay(1)
    );

    const vault = this.getVault().pipe(
      map((address) => new RetiredVault(address, this.accountService, this.contractService, {})),
      sharePublishReplay(1)
    );

    this.totalShares = token.pipe(switchMap((t) => t.totalSupply));
    this.equity = vault.pipe(
      switchMap((v) => timer(0, 5000).pipe(switchMap(() => v.getBalance(this.contract)))),
      sharePublishReplay(1)
    );
    this.myShares = token.pipe(switchMap((t) => t.balance));
    this.myValue = combineLatest([this.equity, this.totalShares, this.myShares]).pipe(
      map(([e, t, m]) => (t.eq(0) ? new BigNumber(0) : e.div(t).times(m)))
    );
    this.staked = this.myShares.pipe(map((s) => s.gt(0)));
  }

  getToken() {
    return this.call((i) => i.methods.token());
  }

  getVault() {
    return this.call((i) => i.methods.vault());
  }

  withdrawAll(paymasterMethod?: PaymasterMethod) {
    return combineLatest([this.myShares, this.myValue]).pipe(
      take(1),
      mergeMap(([s, v]) =>
        this.send((i) => i.methods.withdraw(s.toDecimal().toString(10), v.times(0.99).toDecimal().toString(10)), {
          paymasterMethod,
          extraData: signedData
        })
      )
    );
  }
}
