import BigNumber from 'bignumber.js';
import VaultAbi from 'src/app/abi/vault';
import { AccountService } from '../../services/account.service';
import { ContractService } from '../../services/contract.service';
import { Base } from '../base';
import { Observable, combineLatest, of, timer } from 'rxjs';
import { map, mergeMap, switchMap, take } from 'rxjs/operators';
import { sharePublishReplay } from 'ngx-libs';
import { PaymasterMethod } from '../paymaster-method';

export const signedData = '';

export class RetiredVault extends Base {
  name: string;
  balance: Observable<BigNumber>;
  deposited: Observable<boolean>;

  constructor(
    public contract: string,
    protected accountService: AccountService,
    protected contractService: ContractService,
    private info: any
  ) {
    super(contract, VaultAbi, accountService, contractService);
    this.name = this.info.name;

    const source = combineLatest([this.accountService.account, timer(0, 5000)]).pipe(sharePublishReplay(1));

    this.balance = source.pipe(
      switchMap(([a]) => (a ? this.getBalance(a).pipe() : of(new BigNumber(0)))),
      sharePublishReplay(1)
    );

    this.deposited = this.balance.pipe(map((b) => b.gt(0)));
  }

  withdrawAll(paymasterMethod?: PaymasterMethod) {
    return this.balance.pipe(
      take(1),
      mergeMap((b) =>
        this.send((i) => i.methods.withdraw(b.toDecimal().toString(10)), { paymasterMethod, extraData: signedData })
      )
    );
  }

  getBalance(account: string) {
    return this.call((i) => i.methods.balanceOf(account)).pipe(map((v) => new BigNumber(v).fromDecimal()));
  }
}
